@import url(https://fonts.googleapis.com/css?family=Titan+One);
@import url(https://fonts.googleapis.com/css?family=Titan+One);
@import url(https://fonts.googleapis.com/css?family=Roboto);
html {
  width: 100%;
  position: relative;
  margin: 0;
  padding: 0;
  /*height: 100vh;*/
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background: #211920;
  width: 100%;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  width: 100%;
  position: relative;
  margin: 0;
  padding: 0;
}
.App-header {
  display: grid;
  justify-content: center;
  grid-gap: 20px;
  padding: 20px;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
}
.title {
  font-family: 'Titan One', cursive;
  text-align: center;
  font-size: 5em;
  margin: 0;
  grid-column: 2 / span 1;
}
.bubble {
  color: #e5c5c4;
}
.study {
  justify-self: center;
  font-family: 'Titan One', cursive;
  text-decoration: none;
  color: white;
  font-size: 1em;
  text-align: center;
  background: #c17cb3;
  padding: 10px;
  border-radius: 15px;
}
.study:hover {
  font-family: 'Titan One', cursive;
  text-decoration: none;
  color: white;
  text-align: center;
  background: #97638c;
  padding: 10px;
  border-radius: 15px;
  box-shadow: inset -4px -3px 2px white
}
.shimmer {
  text-align: center;
  color: rgba(255, 255, 255, 0.1);
  background: -webkit-gradient(linear, left top, right top, from(#c17cb3), to(#c17cb3), color-stop(0.5, #fff));
  background: -moz-gradient(linear, left top, right top, from(#c17cb3), to(#c17cb3), color-stop(0.5, #fff));
  background-size: 30px 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  -webkit-animation-name: shimmer;
  animation-name: shimmer;
  -webkit-animation-duration: 4s;
  animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-color: #c17cb3;
}

.spinner {
  align-content: center;
  margin-top: 100px;
  position: fixed;
  left: 36%;
}
.star1, .star2 {
  background-color: #e5c5c4;
  -webkit-clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  position: absolute;
    width: 101px;
  height: 101px;
  
  -webkit-animation: sk-cubemove 1.8s infinite ease-in-out;
  animation: sk-cubemove 1.8s infinite ease-in-out;
}

.star2 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

@-webkit-keyframes sk-cubemove {
  25% { -webkit-transform: translateX(300px) rotate(-90deg) scale(0.5) }
  50% { -webkit-transform: translateX(300px) translateY(300px) rotate(-180deg) }
  75% { -webkit-transform: translateX(0px) translateY(300px) rotate(-270deg) scale(0.5) }
  100% { -webkit-transform: rotate(-360deg) }
}

@keyframes sk-cubemove {
  25% { 
    transform: translateX(300px) rotate(-90deg) scale(0.5);
    -webkit-transform: translateX(300px) rotate(-90deg) scale(0.5);
  } 
  50% { 
    transform: translateX(300px) translateY(300px) rotate(-179deg);
    -webkit-transform: translateX(300px) translateY(300px) rotate(-179deg);
  } 
  50.1% { 
    transform: translateX(300px) translateY(300px) rotate(-180deg);
    -webkit-transform: translateX(300px) translateY(300px) rotate(-180deg);
  } 
  75% { 
    transform: translateX(0px) translateY(300px) rotate(-270deg) scale(0.5);
    -webkit-transform: translateX(0px) translateY(300px) rotate(-270deg) scale(0.5);
  } 
  100% { 
    transform: rotate(-360deg);
    -webkit-transform: rotate(-360deg);
  }
}

@-webkit-keyframes shimmer {
  0% {
      background-position: top left;
  }
  100% {
      background-position: top right;
  }
}

@keyframes shimmer {
  0% {
      background-position: top left;
  }
  100% {
      background-position: top right;
  }
}

@media (min-width: 320px) and (max-width: 540px) {
  .App-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
  .title {
    font-size: 3em;
    display: flex;
    flex-direction: column;
  }
  .study{
    margin-top: 15px;
  }
  .spinner {
    left: 5px;
  }

  @-webkit-keyframes sk-cubemove {
  25% { -webkit-transform: translateX(200px) rotate(-90deg) scale(0.5) }
  50% { -webkit-transform: translateX(200px) translateY(200px) rotate(-180deg) }
  75% { -webkit-transform: translateX(0px) translateY(200px) rotate(-270deg) scale(0.5) }
  100% { -webkit-transform: rotate(-360deg) }
}

@keyframes sk-cubemove {
  25% { 
    transform: translateX(200px) rotate(-90deg) scale(0.5);
    -webkit-transform: translateX(200px) rotate(-90deg) scale(0.5);
  } 
  50% { 
    transform: translateX(200px) translateY(200px) rotate(-179deg);
    -webkit-transform: translateX(200px) translateY(200px) rotate(-179deg);
  } 
  50.1% { 
    transform: translateX(200px) translateY(200px) rotate(-180deg);
    -webkit-transform: translateX(200px) translateY(200px) rotate(-180deg);
  } 
  75% { 
    transform: translateX(0px) translateY(200px) rotate(-270deg) scale(0.5);
    -webkit-transform: translateX(0px) translateY(200px) rotate(-270deg) scale(0.5);
  } 
  100% { 
    transform: rotate(-360deg);
    -webkit-transform: rotate(-360deg);
  }
}
}

@media (min-width: 540px) and (max-width: 726px) {
