.popup-outer {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0, 0.5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  font-family: 'Roboto'; 
}

.popup-inner {
  position: absolute;
  left: 15%;
  right: 15%;
  top: 15%;
  bottom: 15%;
  background: #e5c5c4;
  overflow-y: scroll;
  display: grid;
  justify-items: center;
  padding: 25px;
  border-radius: 25px;
  grid-template-areas:
  'a a a' 
  'e e e'
  'b b b'
  'c c c';
}
.response {
  grid-area: a;
  color: #ba57a6;
  font-family: 'Titan One';
  font-size: 2em;
}
.pop-up-image {
  background-size: cover;
  border-radius: 10px;
  height: 500px;
  width: 500px;
  grid-area: b;
}
.description {
  grid-area: c;
  color: #ba57a6;
  font-family: 'Roboto';
  font-size: 1.5em;
  padding: 50px; 
}
.next-question {
  position: absolute;
  left: 25px;
  top: 25px;
  background: #ba57a6;
  border: none;
  color: white;
  height: 35px;
  width: 35px;
  font-size: 1.5em;
  border-radius: 50%;
}
.name-star {
  grid-area: e;
  text-align: center;
  color: #ba57a6;
  font-size: 2em;
  border-radius: 15px;
}

@media (min-width: 320px) and (max-width: 540px) {
  .popup-inner {
    left: 5%;
    right: 5%;
    top: 5%;
    bottom: 5%;
    display: block;
    background: #e5c5c4;
    overflow-x: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .pop-up-image{
    height: 300px;
    width: 300px;
  }
  .next-question {
    top: 15px;
  }
  .description{
    padding: 0;
  }
}



.game {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.game-form {
  color: white;
  display: grid;
  background: #c17cb3;
  justify-items: center;
  grid-gap: 10px;
  padding: 15px;
  border-radius: 20px;
  font-size: 1.5em;
  font-family: 'Roboto', sans-serif;
}
.bubble-image {
  height: 600px;
  width: 600px;
  border-radius: 50%;
  display: block;
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px #ebc;
  background-size: cover;
}
.submit {
  font-family: 'Titan One', cursive;
  color: white;
  background: #864385;
  border-radius: 30px;
  width: 150px;
  font-size: 1em;
  border: none;
  height: 45px;
}

.submit:hover {
  box-shadow: inset -4px -3px 2px white;
  background: #512650;
}

