@import url('https://fonts.googleapis.com/css?family=Roboto');

.game {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.game-form {
  color: white;
  display: grid;
  background: #c17cb3;
  justify-items: center;
  grid-gap: 10px;
  padding: 15px;
  border-radius: 20px;
  font-size: 1.5em;
  font-family: 'Roboto', sans-serif;
}
.bubble-image {
  height: 600px;
  width: 600px;
  border-radius: 50%;
  display: block;
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px #ebc;
  background-size: cover;
}
.submit {
  font-family: 'Titan One', cursive;
  color: white;
  background: #864385;
  border-radius: 30px;
  width: 150px;
  font-size: 1em;
  border: none;
  height: 45px;
}

.submit:hover {
  box-shadow: inset -4px -3px 2px white;
  background: #512650;
}

@media (min-width: 320px) and (max-width: 540px) {
  .game {
    flex-direction: column;
    margin-top: 20px;
  }
  .bubble-image {
    height: 300px;
    width: 300px;
  }
  .game-form {
    margin: 20px;
  }
}
